import { Component } from '@angular/core';
import { NgReduxModule, NgRedux, select } from '@angular-redux/store';
// import { } from 'redux-observable'
import { IAppState } from './store/state';
import { EAction } from './store/actions';
import {Observable} from 'rxjs/Observable';
// import { createLogger } from 'redux-logger';
// import { rootReducer } from './reducers';
//curl -u syncomynco https://api.github.com/users/syncomynco/repos

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  title = 'app';
  @select() cnt: Observable<number>;
  @select() msg: Observable<string>;

  constructor(private ngRedux: NgRedux<IAppState>){}

  onClick() {
    this.ngRedux.dispatch({ type: EAction.INC, extras: 'ASDASDASD' });
  }

  onClick2() {
    this.ngRedux.dispatch({ type: 'asdasd', extras: 'ASDASDASD' });
  }
}



