import { IAppState } from './state';
import { EAction, IAction } from './actions';

export function rootReducer(state: IAppState,action: IAction){
    switch(action.type){
        case EAction.INC :
            return { cnt : state.cnt + 1, msg :''}
        case EAction.DEC: 
            return { cnt : state.cnt - 1, msg :''}
            
        
    }

    if(action.type !== EAction){
      return { cnt : null , msg: 'Not my state!'};
    }

    return state;
}


